
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: "login",
components: {
XInput,
Group,
XButton
},
data(){
return{
loginname:"",
password:"",
nickname:"",
msg:"hello world"
}
},
methods:{
login(){
this.axios.get(`https://musicapi.leanapp.cn/login/cellphone?phone=`+this.loginname+`&password=`+this.password).then((response) => {
console.log(response.data.profile.nickname)
this.nickname = response.data.profile.nickname
})
}
},
beforeCreate:function(){
console.log("beforeCreate: ")
console.log(this.msg)
console.log(document.getElementsByClassName("myp")[0])
},
created:function(){
console.log("created: ")

this.msg+='!!!'
console.log(this.msg)
console.log(document.getElementsByClassName("myp")[0])
},
beforeMount:function(){
console.log("beforeMount: ")

this.msg+='@@@@'
console.log(this.msg)
console.log(document.getElementsByClassName("myp")[0])
},
mounted:function(){
console.log("mounted: ")

console.log(this.msg)
console.log(document.getElementsByClassName("myp")[0]) 
},
beforeUpdate:function(){

console.log('beforeUpdate:')
console.log('vuedomdomdomdiff')
},
updated:function(){

console.log('updated:domrender')
},
beforeDestroy:function(){
console.log('beforeDestory:$destroy,:...')
},
destroyed:function(){
console.log('destroyed:...,dom')
}
}



